.header{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #02062e;
    position: sticky;
    top: 0;
    z-index: 100;
    zoom: 67%;
}
.header_logo{
    display: flex;
    height: 50px;
    align-items: center;
    color: rgb(255, 255, 255);
}
.header_home{
    display: flex;
    align-items: center;
    color: rgb(255, 255, 255);
}

.header_nav{
    display: flex;
    justify-content: space-evenly;
}

.header_op{
    display: flex;
    flex-direction: center;
    margin-left: 10px;
    margin-right: 10px;
    color: rgb(255, 255, 255);
}
.line2{
    display: flex;
    flex-direction: center;
    font-size: 17px;
    font-weight: 700;
    margin-left: 10px;
    margin-right: 10px;
    color: rgb(255, 255, 255);
}