.Addtask {
  background-color: rgb(255, 255, 255);
  height: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  zoom: 67%;
}

.Addtask_container {
width: 300px;
height: fit-content;
display: flex;
flex-direction: column;
border-radius: 5px;
border: 1px solid lightgray;
padding: 20px;
}

.Addtask_container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}

.Addtask_container  > form > h2 {
  margin-bottom: 5px;
}

.Addtask_container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
}

.Addtask_submitButton{
  background: #6885e2;;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #3466a8 #21238a #2e1869;
}