.TasksAdded{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: rgb(238, 202, 156);
    z-index: 1;
}
.Task_info{
    height: flex;
    margin-bottom: 15px;
}
.Task_payment{
    margin-top: 5px;
}
.TasksAdded > button {
    background: rgb(247, 180, 156);
    border: 1px solid;
    margin-top: 10px;
    border-color: rgb(180, 32, 62);
    color: rgb(99, 60, 60);
}