table{
    border-collapse: collapse;
    border-spacing: 0;
    border: 2px solid rgb(33, 7, 82);

}

tr {
    background-color: #4b54d8;
    color: white;
  }

tr:nth-child(even) {background-color: #f2f2f2; color: black;}
tr:nth-child(odd) {background-color: #8aafff; color: black;}

.td, .th, .tr {
  flex-shrink: 1 !important;
  min-width: auto !important;
  margin: 0;
}

.container{
  zoom: 67%;
}
.tbody {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 250px;
}

.resizer {
  right: 0;
  background: rgb(255, 255, 255);
  width: 10px;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 1;
  touch-action :none;
}

